import image01 from 'assets/housewarming_01.jpeg';
import image02 from 'assets/housewarming_02.jpeg';
import image03 from 'assets/housewarming_03.jpeg';
import image04 from 'assets/housewarming_04.jpeg';
import imageMo01 from 'assets/mo_housewarming_01.jpeg';
import imageMo02 from 'assets/mo_housewarming_02.jpeg';
import imageMo03 from 'assets/mo_housewarming_03.jpeg';
import imageMo04 from 'assets/mo_housewarming_04.jpeg';
import SectionTitle from 'components/ui/SectionTitle';
import LinkOverlay from 'components/ui/LinkOverlay';
import PhotoCard from 'components/ui/PhotoCard';
import SectionWrap from 'components/sections/SectionWrap';
import styled from 'styled-components';
import { textStyle } from 'styles/mixins/text-style';
import { MD } from 'styles/mixins/styled-responsive';
import { Highlight } from 'styles/globals';
import { lineClamp } from 'styles/mixins/line-clamp';
import { v4 as uuidv4 } from 'uuid';

export default function SectionHousewarming() {
  const lists = [
    {
      id: uuidv4(),
      titleHighlight: '하나하나 차곡차곡',
      title: '여전히 느리지만 담백한 심플라이프',
      imageSrc: image01,
      imageMoSrc: imageMo01,
    },
    {
      id: uuidv4(),
      titleHighlight: '누워서 식멍할 수 있는 4평',
      title: '방을 꽉 채운 식물과 조명💡',
      imageSrc: image02,
      imageMoSrc: imageMo02,
    },
    {
      id: uuidv4(),
      titleHighlight: '확장 후 넓고 환해진 개인 공간',
      title: '라이프스타일도 달라졌어요',
      imageSrc: image03,
      imageMoSrc: imageMo03,
    },
    {
      id: uuidv4(),
      titleHighlight: '제주의 한 숲속 외딴 구옥을',
      title: '셀프로 고친 공예가의 주택',
      imageSrc: image04,
      imageMoSrc: imageMo04,
    },
  ];

  return (
    <section>
      <SectionWrap>
        <SectionTitle title={`따끈따끈🍚 새 집들이가 올라왔어요!`} />
        <div>
          <div>
            <List>
              {lists.map((item) => {
                return (
                  <Item key={item.id}>
                    <LinkOverlay
                      href={''}
                      ariaLabel={`${item.titleHighlight}${item.title}`}
                    />
                    <PhotoCard
                      item={item}
                      scrapButton={true}
                      cssName={'housewarming'}
                    />
                    <Title>
                      <Highlight>{item.titleHighlight}</Highlight>&nbsp;
                      {item.title}
                    </Title>
                  </Item>
                );
              })}
            </List>
          </div>
        </div>
      </SectionWrap>
    </section>
  );
}

const List = styled.ul`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;

  ${MD`
      grid-template-columns: repeat(4, 1fr);
      gap: 16px;
  `}
`;

const Item = styled.li`
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  gap: 10px;
`;

const Title = styled.div`
  ${textStyle(16)}
  ${lineClamp(2)}
    word-break: keep-all;

  ${Highlight} {
    font-weight: 700;
  }
`;
