import SectionTitle from 'components/ui/SectionTitle';
import SectionWrap from 'components/sections/SectionWrap';
import BestItem from './BestItem';
import styled from 'styled-components';
import useFetch from 'hooks/use-fetch';
import { useState } from 'react';
import ProductCategoryControlSlide from './ProductCategoryControlSlide';
import { v4 as uuidv4 } from 'uuid';

const categoryList = [
  { id: uuidv4(), all: '전체' },
  { id: uuidv4(), christmas: ' 크리스마스' },
  { id: uuidv4(), furniture: '가구' },
  { id: uuidv4(), fabrics: '패브릭' },
  { id: uuidv4(), homeAppliances: '가전·디지털' },
  { id: uuidv4(), kitchenware: '주방용품' },
  { id: uuidv4(), food: '식품' },
  { id: uuidv4(), decor: '데코·식물' },
  { id: uuidv4(), lighting: '조명' },
  { id: uuidv4(), storage: '수납·정리' },
  { id: uuidv4(), householdGoods: '생활용품' },
  { id: uuidv4(), dailyNecessities: '생필품' },
  { id: uuidv4(), kids: '유아·아동' },
  { id: uuidv4(), pets: '반려동물' },
  { id: uuidv4(), camping: '캠핑·레저' },
  { id: uuidv4(), tools: '공구·DIY' },
  { id: uuidv4(), interior: '인테리어시공' },
  { id: uuidv4(), rental: '렌탈·구독' },
  { id: uuidv4(), grocery: '장보기' },
  { id: uuidv4(), wedding: '혼수·신혼' },
];

export default function SectionBestProduct() {
  const [data, loading, error] = useFetch('best-products');
  const [category, setCategory] = useState('all');

  const handleCategory = (e) => {
    const value = e.target.textContent;
    const obj = categoryList.find((v) => {
      return Object.values(v)[1] === value;
    });
    const key = Object.keys(obj)[1];
    setCategory(key);
  };

  return (
    <section>
      <SectionWrap>
        <SectionTitle title={'베스트'} />
        <div>
          <ProductCategoryControlSlide
            categoryList={categoryList}
            handleCategory={handleCategory}
            category={category}
          />
          <div>
            <Group>
              {data[category] &&
                data[category].map((item) => {
                  return (
                    <BestItem
                      key={item.id}
                      cssName={'best-product'}
                      item={item}
                    />
                  );
                })}
            </Group>
          </div>
        </div>
      </SectionWrap>
    </section>
  );
}

const Group = styled.div`
  display: flex;
  gap: 12px;
`;
