export default function BannerSlide() {
  const [currentIndex, setCurrentIndex] = useState(1);

  const isMobile = useMediaQuery({ query: '(max-width: 767px)' });

  function setSlideIndex(swiper) {
    setCurrentIndex(swiper.realIndex + 1);
  }

  return (
    <StyledBannerSlide className="banner-slide">
      <SlideArrowButtons />
      <StyledSwiper
        modules={[Navigation, Autoplay]}
        autoplay={{
          pauseOnMouseEnter: true,
        }}
        navigation={{
          nextEl: '.banner-slide .next-button',
          prevEl: '.banner-slide .prev-button',
        }}
        loop={true}
        onRealIndexChange={setSlideIndex}
      >
        {slidesItems.map((item) => {
          const { id, ariaLable, imageSrc, imageMoSrc } = item;

          return (
            <StyledSwiperSlide key={id}>
              <a href="" aria-label={ariaLable}>
                <img src={isMobile ? imageMoSrc : imageSrc} alt="" />
              </a>
            </StyledSwiperSlide>
          );
        })}
      </StyledSwiper>
      <NumberGroup>
        <span>{currentIndex}</span>/<span>{slidesItems.length}</span>
      </NumberGroup>
    </StyledBannerSlide>
  );
}

const StyledBannerSlide = styled.div`
  position: relative;
  overflow: hidden;
  border-radius: 4px;

  .prev-button,
  .next-button {
    opacity: 0;
  }

  &:hover {
    .prev-button,
    .next-button {
      opacity: 1;
    }
  }

  ${MD`
    overflow: visible;
  `}
`;

const StyledSwiper = styled(Swiper)`
  border-radius: 4px;
  overflow: hidden;
`;

const StyledSwiperSlide = styled(SwiperSlide)`
  a {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    overflow: hidden;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`;

const NumberGroup = styled.a`
  ${textStyle(12)}
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1;
  padding: 2px 10px;
  font-weight: 700;
  color: ${({ theme }) => theme.colors.white};
  background: ${({ theme }) => theme.colors.background5};
  border-top-left-radius: 8px;

  span {
    color: inherit;
  }

  ${MD`
      ${textStyle(14)}

      right: 12px;
      bottom: 12px;
      padding: 4px 10px 4px 12px;
      letter-spacing: -1px;
      border-radius: 20px;

      &::after {
        font-size: 20px;
        line-height: 16px;
        content: '+';
      }

  `}
`;
