import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation } from 'swiper/modules';
import SlideArrowButtons from 'components/ui/SlideArrowButtons';
import styled from 'styled-components';
import { textStyle } from 'styles/mixins/text-style';
import { MD } from 'styles/mixins/styled-responsive';
import useSlideState from 'hooks/use-slide-state';
import imageSchool from 'assets/category/category_school.png';
import imageFurniture from 'assets/category/category_furniture.png';
import imageFabrics from 'assets/category/category_fabric.png';
import imageHomeAppliances from 'assets/category/category_appliances.png';
import imageKitchenware from 'assets/category/category_kitchenware.png';
import imageFood from 'assets/category/category_food.png';
import imageDecor from 'assets/category/category_decor.png';
import imageLighting from 'assets/category/category_lighting.png';
import imageStorage from 'assets/category/category_storage.png';
import imageHouseholdGoods from 'assets/category/category_household_items.png';
import imageDailyNecessities from 'assets/category/category_daily_necessities.png';
import imageKids from 'assets/category/category_kids.png';
import imagePets from 'assets/category/category_pet.png';
import imageCamping from 'assets/category/category_camping.png';
import imageTool from 'assets/category/category_tools.png';
import imageInterior from 'assets/category/category_interior_construction.png';
import imageRental from 'assets/category/category_rental.png';
import imageGrocery from 'assets/category/category_shopping.png';
import imageWedding from 'assets/category/category_wedding.png';
import { v4 as uuidv4 } from 'uuid';

export default function ProductCategoryShortcutSlide() {
  const lists = [
    { id: uuidv4(), title: '새학기 준비', imageSrc: imageSchool },
    { id: uuidv4(), title: '가구', imageSrc: imageFurniture },
    { id: uuidv4(), title: '패브릭', imageSrc: imageFabrics },
    { id: uuidv4(), title: '가전·디지털', imageSrc: imageHomeAppliances },
    { id: uuidv4(), title: '주방용품', imageSrc: imageKitchenware },
    { id: uuidv4(), title: '식품', imageSrc: imageFood },
    { id: uuidv4(), title: '데코·식물', imageSrc: imageDecor },
    { id: uuidv4(), title: '조명', imageSrc: imageLighting },
    { id: uuidv4(), title: '수납·정리', imageSrc: imageStorage },
    { id: uuidv4(), title: '생활용품', imageSrc: imageHouseholdGoods },
    { id: uuidv4(), title: '생필품', imageSrc: imageDailyNecessities },
    { id: uuidv4(), title: '유아·아동', imageSrc: imageKids },
    { id: uuidv4(), title: '반려동물', imageSrc: imagePets },
    { id: uuidv4(), title: '캠핑·레저', imageSrc: imageCamping },
    { id: uuidv4(), title: '공구·DIY', imageSrc: imageTool },
    { id: uuidv4(), title: '인테리어시공', imageSrc: imageInterior },
    { id: uuidv4(), title: '렌탈·구독', imageSrc: imageRental },
    { id: uuidv4(), title: '장보기', imageSrc: imageGrocery },
    { id: uuidv4(), title: '혼수·신혼', imageSrc: imageWedding },
  ];

  const [slideState, handleSlideState] = useSlideState();

  return (
    <StyledSlide className="product-category-shortcut-slide">
      <SlideArrowButtons slideState={slideState} />
      <StyledSwiper
        modules={[Navigation]}
        navigation={{
          nextEl: '.product-category-shortcut-slide .next-button',
          prevEl: '.product-category-shortcut-slide .prev-button',
        }}
        onProgress={(_, progress) => {
          handleSlideState(progress);
        }}
        slidesPerView={'auto'}
        spaceBetween={12}
        watchSlidesProgress={true}
        slidesPerGroupAuto={true}
      >
        {lists.map((item) => {
          const { id, title, imageSrc } = item;

          return (
            <StyledSwiperSlide key={id}>
              <a href="" aria-label={title}>
                <img src={imageSrc} alt="" />
                <span>{title}</span>
              </a>
            </StyledSwiperSlide>
          );
        })}
      </StyledSwiper>
    </StyledSlide>
  );
}

const StyledSlide = styled.div`
  position: relative;
  margin-right: -16px;

  ${MD`
      margin-right: 0;
  `}
`;

const StyledSwiper = styled(Swiper)`
  scroll-snap-type: x mandatory;
`;

const StyledSwiperSlide = styled(SwiperSlide)`
  width: 72px;

  a {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }

  img {
    display: block;
    flex-shrink: 0;
    width: 72px;
    height: 72px;
  }

  span {
    ${textStyle(14)}
    flex-shrink: 0;
    margin-top: 8px;
  }
`;
